.section-title {
  font-family: $ff-alumni-semi-bold;
  font-weight: 600;
  font-size: 54px;
  line-height: 65px;
  letter-spacing: 0.03em;

  color: $color-sapphire;

  &--small {
    font-size: 48px;
    line-height: 58px;

    @include tablet {
      font-size: 42px;
      line-height: 50px;
    }

    @include mobile {
      font-size: 32px;
      line-height: 38px;
    }
  }
}
